.InputGroup {
	cursor: pointer;
	padding: 0.5em;
	border: 1px solid #ccc;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: baseline;
	    align-items: baseline;
}

.InputGroup.InputGroup--highlight-partial {
	background-color: hsl(50, 96%, 90%);
}

.InputGroup.InputGroup--highlight-exact {
	background-color: hsl(50, 96%, 80%);
}

.InputGroup:hover {
	background-color: #e0ebf0;
}

.InputGroup code {
	margin: 0 0.5em;
}

.InputGroup input {
	pointer-events: none;
}



