.FileTypes fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

.FileTypes-radios {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.FileTypes-radios label {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin-right: -1px;
	margin-bottom: -1px;
}

@media (max-width: 1023px) {
	.FileTypes-radios label {
		-ms-flex-preferred-size: 20%;
		    flex-basis: 20%;
	}
}



