.Inputs {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.Inputs-section-octal,
.Inputs-section-decimal {
	-ms-flex: 2 1;
	    flex: 2 1;
}

.Inputs-section-symbolic {
	-ms-flex: 3 1;
	    flex: 3 1;
}

.Inputs-input-wrapper {
	position: relative;
}

.Inputs-input-wrapper input {
	width: 100%;
	font: 3em monospace;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border: 1px solid #ccc;
	border-radius: 0.25rem;
	padding: 1.5em 0.33em 0.33em;
	outline: none;
	-webkit-box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, 0.1);
	        box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, 0.1);
}

.Inputs-input-wrapper input:focus {
	border-color: #09c;
}

.Inputs-input-wrapper h2 {
	position: absolute;
	top: 0.75em;
	left: 0.75em;
	pointer-events: none;
}

@media (max-width: 767px) {
	.Inputs-section {
		width: 100%;
		-ms-flex: auto;
		    flex: auto;
	}

	.Inputs-input-wrapper input {
		font-size: 2em;
		padding: 2.25em 0.5em 0.5em;
	}
}



