.FlagsList label {
	-ms-flex: 1 1;
	    flex: 1 1;
	cursor: pointer;
	padding: 0.5em;
	border: 1px solid #ccc;
	margin-right: -1px;
	margin-bottom: -1px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: baseline;
	    align-items: baseline;
}

.FlagsList label:hover {
	background-color: #e0ebf0;
}



